.regularButton {
  display: flex;
  align-items: center;
  height: 4.8rem;
  width: fit-content;
  border: 1px solid #EAE5D8;
  border-radius: 0.4rem;
  position: absolute;
  text-transform: uppercase;
  background-color: transparent;
  transition: background-color 0.25s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 5;
}
.regularButton a {
  z-index: 5;
  width: 100%;
  height: 100%;
  padding: 0.4rem 2.4rem;
  gap: 0.8rem;
  display: flex;
  align-items: center;
  text-decoration: none;
}
.regularButton svg {
  width: 2.4rem;
  height: 1.6rem;
}
.regularButton svg path {
  stroke: #BB4430 !important;
}
.regularButton svg circle {
  stroke: #BB4430;
}
.regularButton::after {
  content: "";
  width: 100%;
  height: calc(100% + 0.4rem);
  border: 1px solid #EAE5D8;
  border-radius: 0.4rem;
  position: absolute;
  top: 0;
  left: -1px;
  transform: translateY(-0.4rem);
  transition: transform 0.25s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 1;
}
.regularButton::before {
  content: "";
  width: 100%;
  height: calc(100% + 0.6rem);
  border: 1px solid #EAE5D8;
  border-radius: 0.4rem;
  position: absolute;
  top: 0;
  left: -1px;
  transform: translateY(-0.6rem);
  transition: transform 0.25s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 1;
}
.regularButton:hover {
  background-color: rgba(248, 243, 229, 0.15);
  transition: background-color 0.25s cubic-bezier(0.19, 1, 0.22, 1);
}
.regularButton:hover::after {
  transform: translateY(-0.1rem);
  transition: transform 0.25s cubic-bezier(0.19, 1, 0.22, 1);
}
.regularButton:hover::before {
  transform: translateY(0);
  transition: transform 0.25s cubic-bezier(0.19, 1, 0.22, 1);
  transition-delay: 0.1s;
}
@media (max-width: 767px) {
  .regularButton {
    position: relative;
    margin-top: 2.4rem;
  }
}

