.regularButton {
  @extend %title-16;

  display: flex;
  align-items: center;
  height: 4.8rem;
  width: fit-content;
  border: 1px solid $color-grey1;
  border-radius: 0.4rem;
  position: absolute;
  text-transform: uppercase;
  background-color: transparent;
  transition: background-color 0.25s $ease-out-expo;

  z-index: z('bring-front');

  a {
    z-index: z('bring-front');
    width: 100%;
    height: 100%;
    padding: 0.4rem 2.4rem;
    gap: 0.8rem;
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  svg {
    width: 2.4rem;
    height: 1.6rem;

    path {
      stroke: $color-red !important;
    }

    circle {
      stroke: $color-red;
    }
  }

  &::after {
    content: '';
    width: 100%;
    height: calc(100% + 0.4rem);
    border: 1px solid $color-grey1;
    border-radius: 0.4rem;
    position: absolute;
    top: 0;
    left: -1px;
    transform: translateY(-0.4rem);
    transition: transform 0.25s $ease-out-expo;
    z-index: z('content');
  }

  &::before {
    content: '';
    width: 100%;
    height: calc(100% + 0.6rem);
    border: 1px solid $color-grey1;
    border-radius: 0.4rem;
    position: absolute;
    top: 0;
    left: -1px;
    transform: translateY(-0.6rem);
    transition: transform 0.25s $ease-out-expo;
    z-index: z('content');
  }

  &:hover {
    background-color: rgba($color: $color-white, $alpha: 0.15);
    transition: background-color 0.25s $ease-out-expo;

    &::after {
      transform: translateY(-0.1rem);
      transition: transform 0.25s $ease-out-expo;
    }

    &::before {
      transform: translateY(0);
      transition: transform 0.25s $ease-out-expo;
      transition-delay: 0.1s;
    }
  }

  @include media('<phone') {
    position: relative;
    margin-top: 2.4rem;
  }
}

